import React, {createContext, useReducer, useEffect} from 'react';
import axios from 'axios';
const intialState = [];
export const ExpenseTrackerContext = createContext(intialState);

const reducer = (state = intialState, action) => {
    let transactions;

    switch(action.type) {
        case "FETCH_TRANSACTION" :
            console.log(action.payload);
            transactions = [...action.payload];
            return transactions;

        case "DELETE_TRANSACTION":
            transactions = state.filter((t)=> t.id != action.payload);
            axios.delete('/deletetransaction?id='+action.payload)
            return transactions;

        case 'ADD_TRANSACTION':
            axios.post('/addtransaction',action.payload).then((response) => {
                if(response.status === 200) {
                    console.log(state, action.payload, 'in reducer')
                    return [action.payload, ...state];
                }
                return [...state];
            })   
        default:
            return [...state];
    }
}

export const Provider = ({ children }) =>{
    const [transactions, dispatch] = useReducer(reducer, intialState);

    const deleteTransaction = (id)=>{
        dispatch({type : 'DELETE_TRANSACTION', payload: id});
        window.location.reload();
    }

    const addTransaction = (transaction)=>{
        console.log(transaction, transactions, 'add transaction');
        dispatch({type : 'ADD_TRANSACTION', payload: transaction});
        window.location.reload();
    }

    const fetchTransactions = ()=>{
        axios.get('/gettransactions').then((response)=>{
            console.log(response.data);
            dispatch({type : 'FETCH_TRANSACTION', payload: response.data.data})
        })    
    }

    useEffect(()=>{
        fetchTransactions();
    },[])

    const balance = transactions.reduce((acc,current)=>{
        return (current.type == 'Expense' ? acc-current.amount : acc+current.amount)
    },0);
    
    return(
        <ExpenseTrackerContext.Provider value={{deleteTransaction,addTransaction,transactions, balance}}>
            {children}
        </ExpenseTrackerContext.Provider>
    )
}
